<template>
  <v-container fluid grid-list-xl>
    <!-- {{ this.current_category_value }}
    <br />
    {{ this.current_category_text }}
    <br />
    {{ this.current_problem }}
    <br />-->
    <v-layout wrap align-center>
      <v-flex xs3 d-flex>
        <v-select
          :items="quiz.categories"
          :label="quiz.categories_label"
          @change="getCategory"
          solo
        ></v-select>
      </v-flex>

      <v-flex xs3 d-flex>
        <v-select
          :items="quiz.problems[current_category_value]"
          :label="quiz.problems_label"
          @change="getProblem"
          solo
        ></v-select>
      </v-flex>
    </v-layout>
    <v-layout>
      <router-view></router-view>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    current_category_text: "2016-03-05",
    current_category_value: "0",
    current_category: "concept",
    current_problem: "1",
    quiz: {
      categories_label: "",
      categories: [
        { text: "2016-03-05_", value: 0 },
        { text: "2016-10-29_", value: 1 },
        { text: "2017-03-04_", value: 2 },
        { text: "2016-03-05_", value: 3 },
        { text: "2016-10-29_", value: 4 },
        { text: "2017-03-06_", value: 5 },
        { text: "2017-10-28_", value: 6 }
      ],
      problems_label: "",
      problems: [
        Array.from({ length: 25 }, (v, i) => i + 1).map(String),
        Array.from({ length: 25 }, (v, i) => i + 1).map(String),
        Array.from({ length: 25 }, (v, i) => i + 1).map(String),
        ["1-", "2-", "3-", "4-"],
        ["1-", "2-", "3-K", "4-"],
        ["1-", "2-", "3-", "4-"],
        ["1-", "2-", "3-", "4-"]
      ]
    }
  }),
  methods: {
    getCategory(category) {
      this.current_category_value = category;
      this.current_category_text = this.quiz.categories[category].text.split(
        `_`
      )[0];
      this.current_category = this.quiz.categories[category].text.split(`_`)[1];
    },
    getProblem(problem) {
      this.current_problem = problem.split(`-`)[0];
      if (this.current_category == "") {
        // alert("");
        this.$router.push(
          `/courses/apcs/archive/concept/${this.current_category_text}/${
            this.current_problem
          }`
        );
      } else if (this.current_category == "") {
        // alert("");
        this.$router.push(
          `/courses/apcs/archive/implement/${this.current_category_text}/${
            this.current_problem
          }`
        );
      }
    }
  },
  created: function() {
    this.$router.push(
      `/courses/apcs/archive/concept/${this.current_category_text}/${
        this.current_problem
      }`
    );
  }
};
</script>

<style lang="scss" scoped></style>
